import TableCommon from 'components/TableCommon';

import { getTodayDate } from 'utils/getTodayDate';

const UserInfoTable = ({ items, dateToFetch }) => {
  // console.log(206.2 / 20);

  const dateData =
    dateToFetch === getTodayDate()
      ? { label: '', value: ['dateData', 'hours'], obj: true }
      : { label: '', value: ['dateData', 'date'], obj: true };

  const init = {
    initItems: [
      { label: '', value: 'goods' },
      { label: '', value: 'bonus' },
      dateData,
      { label: '', value: 'weight' },
    ],
  };

  //   console.log(format(items[0].date, 'dd MM yyyy, HH:mm'));

  return (
    <TableCommon
      init={init}
      items={items}
      // items={items.map(el => ({
      //   ...el,
      //   weight:
      //     el.goods === 'Miocane' || el.goods === 'Miogato'
      //       ? '-'
      //       : el.bonus / 20,
      // }))}
    />
  );
};

export default UserInfoTable;
